raul-simple-table {
  display: block;
  font-size: 0.875rem;
}

raul-simple-table table {
  width: 100%;
  background-color: #fff;
  border-collapse: collapse;
  font-size: 0.875rem;
  color: #37474F;
}

raul-simple-table table th, raul-simple-table table td {
  border-bottom-width: 1px;
  border-color: #EBEDEE;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

raul-simple-table table th {
  text-align: left;
  font-size: 0.75rem;
  font-weight: 700;
  background-color: #F7F8F9;
}

raul-simple-table[small] table tbody td {
  font-size: 0.75rem;
}

raul-simple-table[striped] table tbody tr:nth-child(even) {
  background-color: #F7F8F9;
}

raul-simple-table[hoverable] table tbody tr:hover {
  background-color: #E5F4FF;
}